import * as React from 'react';
import Layout from '../components/common/Layout/Layout';
import ArticleCardItem, {
  CardSize,
} from '../components/common/ArticleCardItem/ArticleCardItem';
import { NewsPageHeader } from '../components/news';
import { graphql, PageProps } from 'gatsby';
import { mapArticleNodeToArticlePreviewInfo } from '../mappers';
import { useLunr } from 'react-lunr';
import { ArticlePreviewInfo } from '../types';
import {
  GatsbySeoComponent,
  MetaData,
  TopLevelPageJSONLDSchemas,
} from '../components/common/SEO';
import {
  ArticleListContentContainer,
  ArticleList,
  DesktopCardsContainer,
  MediumCards,
  MobileCardsContainer,
  NoResultsText,
  SmallCards,
  SmallCardWrapper,
} from '../components/common/styled';
import { useInView } from 'react-cool-inview';
import useWindowSize, { isDesktop } from '../hooks/useWindowSize';
import { StaticPageFrontmatter } from '../types/StaticPageFrontmatter';
import SubscribeBlock from '../components/common/SubscribeBlock';
import { InlineSubscribeBlock, MobileInlineSubscribeBlock } from '../components/news/styled';

const NewsPage = ({ data }: PageProps<Queries.NewsPageQuery>) => {
  const [searchString, setSearchString] = React.useState<string>('');
  const [articlesForRenderAmount, setArticlesForRenderAmount] = React.useState(10);

  const rawNews: ArticlePreviewInfo[] = React.useMemo(() => {
    return data.allMdx.edges.map((edge) => mapArticleNodeToArticlePreviewInfo(edge.node));
  }, []);

  const filteredNews: ArticlePreviewInfo[] = useLunr(
    searchString
      .trim()
      .split(' ')
      .map((word) => `${word} ${word}*`)
      .join(' '),
    data.localSearchNews?.index,
    data.localSearchNews?.store,
  ) as ArticlePreviewInfo[];

  const news = searchString
    ? filteredNews.sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime())
    : rawNews;

  const sortedNews = [
    ...news
      .filter((article) => article.sortByUpdatedDate)
      .sort(
        (a, b) =>
          new Date(b.lastUpdatedOn).getTime() - new Date(a.lastUpdatedOn).getTime(),
      ),
    ...news.filter((article) => !article.sortByUpdatedDate),
  ];

  const separatedNewsByCardSize = {
    largeCard: sortedNews.slice(0, 1),
    mediumCards: sortedNews.slice(1, 5),
    smallCards: sortedNews.slice(5, articlesForRenderAmount),
  };

  const { observe } = useInView({
    // For better UX, we can grow the root margin so the data will be loaded earlier
    rootMargin: '50px 0px',
    // When the last item comes to the viewport
    onEnter: () => {
      setArticlesForRenderAmount((st) => st + 5);
    },
  });

  const windowSize = useWindowSize();
  const isDesktopScreen = isDesktop(windowSize.width as number);

  const title = 'Blog | Telgorithm';

  return (
    <>
      <MetaData
        title={title}
        description="Stay up to date on the latest news in A2P SMS and 10DLC and learn what it takes to provide the next level of business messaging experiences for your customers."
      />
      <TopLevelPageJSONLDSchemas url="https://www.telgorithm.com/news/" />
      <GatsbySeoComponent canonicalUrl="https://www.telgorithm.com/news/" title={title} />
      <Layout>
        <ArticleListContentContainer>
          <NewsPageHeader searchString={searchString} setSearchString={setSearchString} />
          {sortedNews.length ? (
            <ArticleList>
              <DesktopCardsContainer>
                {separatedNewsByCardSize.largeCard.map((articleItem) => (
                  <ArticleCardItem
                    article={articleItem}
                    key={articleItem.articleId}
                    cardSize={CardSize.L}
                  />
                ))}
                {separatedNewsByCardSize.mediumCards.length > 0 && (
                  <MediumCards>
                    {separatedNewsByCardSize.mediumCards.map((articleItem) => (
                      <ArticleCardItem
                        article={articleItem}
                        key={articleItem.articleId}
                        cardSize={CardSize.M}
                      />
                    ))}
                  </MediumCards>
                )}
                {sortedNews.length >= 5 && (
                  <InlineSubscribeBlock>
                    <SubscribeBlock
                      showBulbIcon={false}
                      containerId="desktopBlogSubscribeForm"
                      title="Subscribe to our newsletter."
                      description="Receive updates from our team including latest industry news, upcoming webinars, 10DLC tips & more."
                    />
                  </InlineSubscribeBlock>
                )}
                {isDesktopScreen && separatedNewsByCardSize.smallCards.length > 0 && (
                  <SmallCards>
                    <>
                      {separatedNewsByCardSize.smallCards.map(
                        (articleItem, index, arr) => (
                          <SmallCardWrapper
                            ref={
                              (index === arr.length - 1 ? observe : null) as (
                                element?: any,
                              ) => void
                            }
                          >
                            <ArticleCardItem
                              article={articleItem}
                              key={articleItem.articleId}
                              cardSize={CardSize.S}
                            />
                          </SmallCardWrapper>
                        ),
                      )}
                    </>
                  </SmallCards>
                )}
              </DesktopCardsContainer>
              <MobileCardsContainer>
                <div>
                  <MediumCards>
                    {[
                      ...separatedNewsByCardSize.largeCard,
                      ...separatedNewsByCardSize.mediumCards,
                    ].map((articleItem) => (
                      <ArticleCardItem
                        article={articleItem}
                        key={articleItem.articleId}
                        cardSize={CardSize.M}
                      />
                    ))}
                  </MediumCards>
                </div>
                {sortedNews.length >= 5 && (
                  <MobileInlineSubscribeBlock>
                    <SubscribeBlock
                      containerId="mobileBlogSubscribeForm"
                      title="Subscribe to our newsletter."
                      description="Receive updates from our team including latest industry news, upcoming webinars, 10DLC tips & more."
                    />
                  </MobileInlineSubscribeBlock>
                )}
                <div>
                  {!isDesktopScreen && separatedNewsByCardSize.smallCards.length > 0 && (
                    <SmallCards>
                      {separatedNewsByCardSize.smallCards.map(
                        (articleItem, index, arr) => (
                          <SmallCardWrapper
                            ref={
                              (index === arr.length - 1 ? observe : null) as (
                                element?: any,
                              ) => void
                            }
                          >
                            <ArticleCardItem
                              article={articleItem}
                              key={articleItem.articleId}
                              cardSize={CardSize.S}
                            />
                          </SmallCardWrapper>
                        ),
                      )}
                    </SmallCards>
                  )}
                </div>
              </MobileCardsContainer>
            </ArticleList>
          ) : (
            <NoResultsText>No results for "{searchString}"</NoResultsText>
          )}
        </ArticleListContentContainer>
      </Layout>
    </>
  );
};

export default NewsPage;

export const query = graphql`
  query NewsPage {
    localSearchNews {
      index
      store
    }
    allMdx(
      filter: { internal: { contentFilePath: { regex: "//news//" } } }
      sort: { frontmatter: { date: DESC } }
    ) {
      edges {
        node {
          frontmatter {
            articleType
            date(formatString: "MMMM DD, YYYY")
            title
            sortByUpdatedDate
            lastUpdatedOn
            previewImage {
              childrenImageSharp {
                gatsbyImageData(
                  layout: FIXED
                  formats: WEBP
                  webpOptions: { quality: 50 }
                  quality: 50
                )
              }
            }
          }
          id
          fields {
            slug
          }
        }
      }
    }
  }
`;

export const frontmatter: StaticPageFrontmatter = {
  title: 'Telgorithm Blog',
  metaTitle: 'Telgorithm Blog',
  excerpt:
    'Stay up to date on the latest news in A2P SMS and 10DLC and learn what it takes to provide the next level of business messaging experiences for your customers.',
  slug: '/news/',
};
